.header {
  max-height: 250px;
  font-size: 0.8rem;
  margin: 0;
  background-color: #ffffff;
  position: relative;
}

.header .students-image-box {
  padding: .4rem;
  background-color: #ffffff;
  border-radius: 50%;
  position: absolute;
  right: 2rem;
  top: 25%;
}

.header .students-image-box img {
  border-radius: 50%;
  width: 120px;
  height: 120px;
}

.separator {
  display: block;
  width: 2px;
  background-color: #b8b8b8;
  margin: 0.3rem 1rem;
  border-radius: 3px;
}

.report {
  background-color: #f3bbf3;
  font-size: 0.93rem;
}

.data {
  text-align: right;
  margin-bottom: 5px;
}

.data-input {
  background-color: #ffffff;
  border-radius: 5px;
  text-transform: capitalize;
}

@media (min-width: 768px) {
  .data-input {
    width: max-content;
    width: fit-content;
    min-width: 100px;
  }
}

.data-input-sm {
  width: 90px;
  padding: 0.1rem 0.3rem;
  background-color: #ffffff;
  margin-left: 10px;
  display: inline-block;
  text-align: center;
}

.data-input-md {
  width: 120px;
  padding: 0.1rem 0.3rem;
  background-color: #ffffff;
  margin-left: 10px;
  display: inline-block;
  text-align: center;
}

.data-input-lg {
  width: 150px;
  padding: 0.1rem 0.3rem;
  background-color: #ffffff;
  margin-left: 10px;
  display: inline-block;
  text-align: center;
}

.stats {
  flex-shrink: 1;
  flex-basis: 75%;
  overflow-x: auto;
}

.stats .chart {
  border-radius: 0 0 0 15px;
}

.table-container {
  padding: 1rem;
  border-radius: 15px 0 0 0;
  background-color: purple;
  margin-top: 1rem;
  font-size: 0.9rem;
  overflow-x: auto;
}

.table-container::-webkit-scrollbar {
  height: 10px !important;
}

.table-container::-webkit-scrollbar-button {
  visibility: hidden !important;
}

.table-container::-webkit-scrollbar-track {
  background-color: rgba(204, 204, 204, 0.712);
  border-radius: 50px !important;
}

.table-container::-webkit-scrollbar-thumb {
  background-color: rgba(165, 165, 165, 0.836) !important;
  border-radius: 50px !important;
}

table.tabbed-report {
  border-collapse: separate;
}

table.tabbed-report thead tr {
  text-align: left;
}

table.tabbed-report tbody tr {
  background-color: #ffffff;
}

table.tabbed-report tbody tr:nth-of-type(even) {
  background-color: whitesmoke;
}

table.tabbed-report th:not(.bottom-up) {
  text-align: left;
  padding: 0rem 1rem 1rem;
  vertical-align: bottom;
  color: #ffffff;
}

table.tabbed-report td {
  padding: 0.8rem 0.4rem;
}

table.tabbed-report td:first-of-type {
  font-weight: 600;
}

table.tabbed-report tr {
  position: relative;
}

table.tabbed-report tr th.bottom-up {
  text-align: center;
  border-top: 0;
  padding: 200px 5px 3px;
  vertical-align: bottom;
  background-color: #ffffff;
}

table.tabbed-report tr th.bottom-up p,
table.tabbed-report tr th.bottom-up span {
  transform-origin: left;
  transform: rotate(-90deg);
  margin-bottom: 20px;
  display: inline-block;
  bottom: 0;
  margin: 0 0 0 10px;
  padding: 0;
  width: 20px;
  height: 20px;
  text-align: left;
  white-space: nowrap;
}

aside .aside-evaluation {
  background-color: purple;
